.row {
    display: -webkit-flex;
    display: flex;
    background-color: #d2d2d2;
    margin: 10px 0;
    padding: 10px;
}

.row div:nth-child(1) {
    width: 20%;
}

.row div:nth-child(2) {
    width: 20%;
    overflow: hidden;
}

.row div:nth-child(3) {
    width: 10%;
    text-align: center;
}

.row div:nth-child(4) {
    width: 10%;
    text-align: center;
}

.row div:nth-child(5) {
    width: 10%;
    text-align: center;
}

.row div:nth-child(6) {
    width: 10%;
    text-align: center;
}

.row.title {
    font-weight: 700;
    text-align: center;
}
header {
    background-color: rgba(14, 8, 8, 0.5);
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
}

h4 {
    text-align: center;
    color: white;
    width: 100%;
}

header a {
    padding: 10px;
    color: yellow;
    text-decoration: none;
    width: 10%;
    text-align: center;
    cursor: pointer;
    border-radius: 10%;
}

header a:hover {
    background-color: rgba(14, 8, 8, 0.8);
}
